.ft {
  text-align: center;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: white;
  border-top: 1px solid #dcdcdc;
  z-index: 99;
  padding: 10px 50px;
}
.ft .ft-btn {
  padding: 8px 40px;
  font-size: 14px;
}
.ft .gray-btn {
  color: #b7b7b7;
}
.ft .deep-color-btn {
  background-color: #333333;
}
.ft .lf-side {
  float: left;
}
.ft .rt-side {
  float: right;
}
.node-creator-container .node-form {
  width: 480px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.node-creator-container .node-form .node-creator-input-item {
  border-bottom: 1px solid #666666;
  font-size: 24px;
}
.node-creator-container .node-form .node-creator-input {
  border: none;
  outline: none;
  font-size: 24px;
  max-width: 100%;
  display: flex;
  flex: 1;
}
.node-creator-container .node-form .node-creator-input::placeholder {
  color: #CCCCCC;
}
.node-creator-container .node-form .node-domain-postfix {
  color: #CCCCCC;
  font-size: 24px;
}
.node-creator-container .ft .ft-btn {
  border-radius: 16px;
}
