.presentables-wrapper {
  display: flex;
}
.presentables-wrapper .presentable-list-container {
  overflow: scroll;
  max-height: 100vh;
  flex-basis: 430px;
  min-width: 430px;
}
.presentables-wrapper .presentable-list-container .dot {
  float: left;
}
.presentables-wrapper .presentable-list-container h4 {
  font-size: 19px;
  margin-top: 65px;
  margin-left: 45px;
  margin-bottom: 18px;
}
.presentables-wrapper .presentable-list-container h4 .el-icon-question {
  margin-right: 12px;
  color: #666666;
}
.presentables-wrapper .presentable-list-container .presentable-item {
  padding: 15px 45px;
  cursor: pointer;
}
.presentables-wrapper .presentable-list-container .presentable-item.active-status-2 .dot,
.presentables-wrapper .presentable-list-container .presentable-item.active-status-2 .p-title {
  opacity: 0.3;
}
.presentables-wrapper .presentable-list-container .presentable-item.active-status-2 .node-res-status-switch {
  display: inline-block;
}
.presentables-wrapper .presentable-list-container .presentable-item.is-presentable-online .dot,
.presentables-wrapper .presentable-list-container .presentable-item.is-presentable-online .p-title {
  opacity: 1;
}
.presentables-wrapper .presentable-list-container .presentable-item:hover {
  background-color: #F5F5F5;
}
.presentables-wrapper .presentable-list-container .presentable-item .node-res-status-switch {
  display: none;
  float: right;
  position: relative;
  z-index: 1;
}
.presentables-wrapper .presentable-list-container .presentable-item.active {
  background-color: #F5F5F5;
  color: #333333;
  font-weight: bold;
}
.presentables-wrapper .presentable-list-container .presentable-item .p-title {
  display: block;
  font-size: 14px;
  padding-left: 25px;
  padding-right: 80px;
  word-break: break-all;
}
.presentables-wrapper .presentable-list-container .add-presentable-btn {
  color: #999999;
  font-size: 14px;
}
.presentables-wrapper .presentable-list-container .add-presentable-btn i {
  margin-right: 10px;
}
.presentables-wrapper .presentable-detail-container {
  background-color: #FAFAFA;
  flex: 1;
}
.presentables-wrapper .presentable-detail-container .presentable-detail-content {
  background-color: #FAFAFA;
}
