.switch-container {
  height: 22px;
  width: 70px;
  border-radius: 12px;
  line-height: 22px;
  position: relative;
  text-align: center;
  cursor: pointer;
}
.switch-container.active-switch {
  background: #89D1AC;
  border: 1px solid #89D1AC;
}
.switch-container.active-switch .switch-label {
  color: white;
  padding-right: 18px;
}
.switch-container.active-switch .switch-mesh {
  border-color: #89D1AC;
  left: 47px;
}
.switch-container.inactive-switch {
  background: #FAFAFA;
  border: 1px solid #CCCCCC;
}
.switch-container.inactive-switch .switch-mesh {
  border-color: #CCCCCC;
  left: -1px;
}
.switch-container.inactive-switch .switch-label {
  color: #CCCCCC;
  padding-left: 18px;
}
.switch-container .switch-mesh {
  display: inline-block;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: white;
  border: 1px solid #CCCCCC;
  position: absolute;
  left: -1px;
  top: -1px;
  transition: left 0.5s;
}
