.node-contracts-container {
  display: flex;
}
.node-contracts-container .contract-list-wrapper {
  padding-top: 60px;
  color: #333333;
  background-color: white;
  min-width: 430px;
  flex-basis: 430px;
  overflow: scroll;
  max-height: 100vh;
  box-shadow: inset 0px -4px 14px -4px #eee;
}
.node-contracts-container .contract-list-wrapper .dot {
  margin-right: 9px;
}
.node-contracts-container .contract-list-wrapper .current {
  background-color: #fafafa;
  border-left: 3px solid #4396F0;
}
.node-contracts-container .contract-list-wrapper .contract-status-1 .dot:after,
.node-contracts-container .contract-list-wrapper .contract-status-3 .dot:after,
.node-contracts-container .contract-list-wrapper .contract-status-5 .dot:after,
.node-contracts-container .contract-list-wrapper .contract-status-6 .dot:after {
  background-color: #F46284;
}
.node-contracts-container .contract-list-wrapper .contract-status-2 .dot:after {
  border-color: #89D1AC;
}
.node-contracts-container .contract-list-wrapper .contract-status-4 .dot:after {
  border-color: #89D1AC;
  background-color: #89D1AC;
}
.node-contracts-container .contract-list-wrapper .contract-list-title {
  font-size: 18px;
  margin-bottom: 35px;
  margin-left: 35px;
}
.node-contracts-container .contract-list-wrapper .contract-list-title i {
  margin-right: 12px;
}
.node-contracts-container .contract-list-wrapper .presentable-item {
  margin-bottom: 10px;
  word-break: break-all;
}
.node-contracts-container .contract-list-wrapper .presentable-name {
  padding-left: 35px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.node-contracts-container .contract-list-wrapper .contract-item {
  padding-left: 65px;
  padding-top: 5px;
  padding-bottom: 5px;
}
.node-contracts-container .contract-list-wrapper .contract-item,
.node-contracts-container .contract-list-wrapper .presentable-name {
  font-size: 14px;
  padding-right: 35px;
  cursor: pointer;
}
.node-contracts-container .contract-list-wrapper .contract-item:hover,
.node-contracts-container .contract-list-wrapper .presentable-name:hover {
  background-color: #F5F5F5;
}
.node-contracts-container .contract-list-wrapper .contract-item .policy-text {
  white-space: pre-wrap;
}
.node-contracts-container .contract-exec-container {
  padding-top: 60px;
  background-color: #FAFAFA;
  border-left: 1px solid #EEEEEE;
  flex: 1;
}
