.pb-list-container {
  color: #333333;
  padding: 50px 25px;
}
.pb-list-container .nav-title {
  font-size: 18px;
  margin-bottom: 30px;
  font-weight: 600;
}
.pb-list-container .pb-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0 40px;
}
.pb-list-container .pb-list .pb-item {
  flex-basis: 50%;
  padding: 20px;
  box-sizing: border-box;
  position: relative;
}
.pb-list-container .pb-list .pb-item.selected .pb-desc .el-icon-circle-check-outline {
  color: #9BCB39;
}
.pb-list-container .pb-list .pb-item.selected .pb-item-wrap {
  border: 5px solid #9BCB39;
  border-radius: 4px;
}
.pb-list-container .pb-list .pb-item-wrap {
  border: 1px solid #eeeeee;
  border-radius: 3px;
  background-color: #FFFFFF;
  transition: all 0.3s;
}
.pb-list-container .pb-list .pb-item-wrap .pb-item-content {
  cursor: pointer;
}
.pb-list-container .pb-list .add-pb-area {
  border: 1px solid #eeeeee;
  border-radius: 3px;
  cursor: pointer;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.pb-list-container .pb-list .add-pb-area:after {
  padding-top: 75%;
  content: ' ';
  display: block;
}
.pb-list-container .pb-list .add-pb-area i {
  color: #999999;
  font-size: 60px;
}
.pb-list-container .pb-list .pb-thumbnail-wrap {
  width: 100%;
  position: relative;
}
.pb-list-container .pb-list .pb-thumbnail-wrap:after {
  content: ' ';
  display: inline-block;
  padding-top: 66.67%;
  width: 100%;
}
.pb-list-container .pb-list .pb-thumbnail-wrap img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.pb-list-container .pb-list .pb-item-content {
  padding: 14px 23px;
  border-top: 1px solid #CCCCCC;
}
.pb-list-container .pb-list .pb-item-content .pb-title {
  height: 19px;
  font-size: 13px;
  margin-bottom: 12px;
}
.pb-list-container .pb-list .pb-desc {
  height: 40px;
}
.pb-list-container .pb-list .pb-desc p {
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.pb-list-container .pb-list .pb-desc .pb-select-btn:hover {
  color: #9e9e9e;
}
.pb-list-container .pb-list .pb-desc .el-icon-circle-check-outline {
  float: right;
  font-size: 40px;
  color: #EEEEEE;
  padding-left: 30px;
}
